.navbar{
    width: 100%;

    height: 60px;
    display: flex;
}
.navbar .logo-s{
    position: absolute;
    height: 50px;
    width: 184.5px;
    left: 7%;
    top: 5px;
    border-radius: 0px;
    cursor: pointer;
}
.navbar-insu{
    margin-left: 50%;
    font-weight: 400;
    font-style: normal;
    font-size: 14px;
    color: #172B4D;
    margin-top: 4px;
    cursor: pointer;
   position: relative;
    

}.custom-shape-divider-top-1672907659 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);
}

.custom-shape-divider-top-1672907659 svg {
    position: relative;
    display: block;
    width: calc(189% + 1.3px);
    height: 89px;
}

.custom-shape-divider-top-1672907659 .shape-fill {
    fill: #ffffff66;
} 
.navbar-vec{
    padding-left: 2px;
    height: 12px;
    width: 12px;
    position: absolute;
    top:25px;
    margin-left: 9%;
     transition: transform .7s ease-in-out;
     cursor: pointer;
}
.navbar-insu:hover .home-ho{
    display:none;
    background-color: red;
}
.navbar-insu:hover .navbar-vec{
    transform: rotate(180deg);
    
}
.navbar-rev {
     margin-left :3%;
    font-weight: 400;
    font-style: normal;
    font-size: 14px;
    color: #172B4D;
    margin-top: 4px;
    cursor: pointer;
   position: relative;

}
.navbar-vec1{
    height: 12px;
    width: 12px;
    top:20px;
    margin-left: 9%;
     transition: transform .7s ease-in-out;
     cursor: pointer;
    position: absolute;
}
.arrow{
    float: inline-end;
}
.navbar-rev:hover .navbar-vec1{
    transform: rotate(180deg);
       
}

